.all {
    width : 100vw;
    height : 100vh;
    z-index : -1;
    animation: appear 1s;
    .formularz {
        width : 100vw;
        position: fixed;
        z-index : 15;
        form {
            width : 100vw;
            display: flex;
            justify-content: center;
            font-size: xx-large;
            input {
                width : 50vw;
                height : 10vh;
                font-size: xx-large;
            }
            button {
                width : 25vw;
                height : 10vh;
                font-size: xx-large;
                border : none;
                font-size: xx-large;
                border : solid 0.2vw #1462ff;
                color : #1462ff;
                background : transparent;
                will-change: color;
                transition: 0.5s;
                z-index : 3;
                position: relative;
                animation : appear 1s ;
                text-align: center;
                text-decoration: none;
                align-items: center;
                font-weight: 600;
                display: inline-block;

            }
            button:hover {
                color :white;
            }
            button::before {
                position: absolute;
                content: '';
                top : 0;
                left : 0; 
                width : 100%;
                height : 100%;
                background-color: #1462ff;
                z-index : -1;  
                transform: scaleX(0); 
                will-change: transform;
                transition: transform 0.8s;
                transform-origin:0 100%;
            }
            button:hover::before {
                transform: scaleX(1);
            }
        }
    }
    .images {
        margin : 0;
        padding : 0;
        display: grid;
        grid-template-columns: repeat(3,1fr);
        grid-template-rows: repeat(99,1fr);
        width : 95vw;
        margin-left:5vw;
        padding-top : 10vh;
        height : 1000vh;
    }
    .modal {
        position: fixed;
        z-index : 1;
        margin-left : 25vw;
        top : 50px;
        animation: appear 1s;
        padding-top : 5vh;
       
    }
    .Modal {
        width : 50vw;
        overflow-y: hidden;
        z-index: 1;
        animation: appear 1s;
        background-color: white;
        img {
            width : 100%;
            height : 100%;
        }
        button {
            width : 100%;
            height : 10%;
            z-index: 3;
            border : none;
            font-size: xx-large;
            border : solid 0.2vw #1462ff;
            color : #1462ff;
            background : transparent;
            will-change: color;
            transition: 0.5s;
            z-index : 3;
            position: relative;
            animation : appear 1s ;
            text-align: center;
            text-decoration: none;
            align-items: center;
            font-weight: 600;
            display: inline-block;
            padding-top: 5px;
        }
        button:hover {
            color : white;
        }
        button::before {
            position: absolute;
            content: '';
            top : 0;
            left : 0; 
            width : 100%;
            height : 100%;
            background-color: #1462ff;
            z-index : -1;  
            transform: scaleX(0); 
            will-change: transform;
            transition: transform 0.8s;
            transform-origin:0 100%;
        }
        button:hover::before{
            transform: scaleX(1);
        }
    }
    
   
        .image{
            width : 25vw;
            height : 30vh;
            border : white solid 0.5vw;
            will-change: transform;
            margin-top : 5vh;
            transition: transform 0.5s;
            animation : appear2 ease-in 1s;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        }
        .image:hover {
            transform : rotate(10deg);
        }
        @media screen and (max-width : 800px){
            .image {
                width : 30vw;
                height: 20vh;
            }
            .images {
                margin-left : 10vw;
                grid-template-columns: repeat(2,1fr);
            }
            .formularz {
                form {
                    button {
                        font-size : large;
                        color : white;
                        background-color: #1462ff;
                    }
                }
            }
            .Modal {
                width : 80vw;
                margin-top : 5vh;
            }
            .modal {
                margin-left : 10vw;
            }
        }
    
}
@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes appear2 {
    0% {
        opacity : 0;
        transform : translateY(35px);
    }
    100% {
        opacity : 1;
        transform : translateY(0);
    }
}
body {
    background-color : rgb(139, 76, 17);
    background-image: url('../../bc2.jpg');
    background-size: cover;
}
