@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');
* , *::before,*::after {
    box-sizing: border-box;
    overflow-x: hidden;
}
body {
    margin : 0;
}
.all {
    width : 100vw;
    height : 100vh;
    background-image: url('../../bc1.jpg');
    background-size: cover;
    display: grid;
    grid-template-rows: repeat(2,1fr);
    z-index: -2;

    .title {
        margin : 0;
        display : flex;
        justify-content: center;
        padding-top: 30vh;
        transform: scale(1.5);
        font-family: 'Montserrat',sans-serif;
        color : #1462ff;
        animation : appear 1s;
        position: relative;
    } }
    .startBtn {
        width : 20vw;
        height : 10vh;
        margin-left: 40vw;
        font-size: xx-large;
        border : solid 0.2vw #1462ff;
        color : #1462ff;
        background : transparent;
        will-change: color;
        transition: 0.5s;
        z-index : 3;
        position: relative;
        animation : appear 1s ;
        text-align: center;
        text-decoration: none;
        align-items: center;
        font-weight: 600;
        display: inline-block;
        padding-top: 5px;
    }

    @media screen and (min-width : 1600px){
        .startBtn{
            padding-top : 10px;
            margin-top : 4vh;
            font-size: 50px;
        }
        .title {
            transform : scale(2.5);
        }
    }
    @media screen and (min-width : 3500px){
        .startBtn{
            padding-top : 10px;
            margin-top : 4vh;
            font-size: 100px;
        }
        .title {
            transform : scale(5);
            padding-top : 27vh;
        }
    }
    .startBtn:hover {
        color : white;
    }
    .startBtn::before {
        position: absolute;
        content: '';
        top : 0;
        left : 0; 
        width : 100%;
        height : 100%;
        background-color: #1462ff;
        z-index : -1;  
        transform: scaleX(0); 
        will-change: transform;
        transition: transform 0.8s;
        transform-origin:0 100%;
    }
    .startBtn:hover::before {
        transform: scaleX(1);
    }
    @media screen and (max-width : 800px){
        .title {
            max-width : 80vw;
            text-align: center;
            padding-left : 13vw;
            transform:scale(1.2);
        }
        .startBtn {
            color : white;
            background-color: #1462ff;
            width : 30vw;
            margin-left : 35vw;
        }
    }
    


@keyframes appear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}